<template>
  <div>
    <v-container class="text-center">
        <br>
      <v-select
        dense
        class="text-center mx-15"
        :items="items"
        v-model="store_id"
        @change="changeStore"
        > {{ data_store.name }}
      </v-select>
      <v-row class="text-right">
            <v-col cols="12" class="mb-n2 text-center">
        <router-link class="mr-2" to="/addstore">
            <v-icon color="#E47911" size="16px" left> mdi-plus </v-icon> Add Store 
        </router-link>
            </v-col>
        </v-row>
        <br>
      <h5 class="text-uoverlinepercase text-center">
        <template>
          <v-chip
            :color="getColor(status)"
            dark
          >
            {{ status }}
          </v-chip>
        </template>
      </h5>
    </v-container>

    <v-container grid-list-md>
    <v-layout row wrap>
      <v-flex xs12 sm4 class="px-sm-3 px-md-8 py-4">
        <router-link to="/sales">
            <v-btn color="#E4E4E4" large block height="100px" width="100%">
            SALES
                <v-icon
                right
                color="#E47911"
                > mdi-currency-usd 
                </v-icon>
            </v-btn>
        </router-link>
        </v-flex>
        <v-flex xs12 sm4 class="px-sm-3 px-md-8 py-4">
        <router-link :to="{ name: 'ViewStore', params: {'id':data_store.unique_id}}">
            <v-btn color="#E4E4E4" large block height="100px" width="100%">
            STORES
                <v-icon
                right
                color="#E47911"
                > mdi-shopping 
                </v-icon>
            </v-btn>
        </router-link>
        </v-flex>
        <v-flex xs6 sm4 class="px-sm-3 px-md-8 py-4" v-for="(item, id) in options" :key="id">
        <router-link :to="item.to">
            <v-btn color="#E4E4E4" large block height="100px">{{ item.title }}              
            <v-icon
            right
            color="#E47911"
            size="20px"
            > {{ item.icon }} 
            </v-icon>
            </v-btn>
        </router-link>
        </v-flex>
        <v-flex xs12 sm4 class="px-sm-3 px-md-8 py-4">
        <a href="https://automationempire.freshdesk.com/support/login">
            <v-btn color="#E4E4E4" large block height="100px" width="100%">
            CUSTOMER <br class="d-md-none">SERVICE
                <v-icon
                right
                color="#E47911"
                > mdi-handshake 
                </v-icon>
            </v-btn>
        </a>
        </v-flex>
    </v-layout>
    </v-container>
  </div>
</template>

<script>

  export default {

    name: 'DashClients',

    data: () => ({
      store_id: "",
      options: [
        {
          title: "PROFILE",
          icon: "mdi-account",
          to: "/profile"
        },
        {
          title: "PAYMENTS",
          icon: "mdi-credit-card-outline",
          to: "/payments"
        },
        {
          title: "REFERRALS",
          icon: "mdi-account-group",
          to: "/referrals"
        },
        {
          title: "INVOICES",
          icon: "mdi-file-document-outline",
          to: "/invoices"
        },
        {
          title: "FAQ'S",
          icon: "mdi-help",
          to: "/faqs"
        },
        {
          title: "SUPPORT",
          icon: "mdi-phone",
          to: "/support"
        },
      ],
      items: [
      ],
    }),

    computed:{
      /* SELECCIONA LA PRIMERA TIENDA DEL USUARIO */
        data_store(){
          return this.$store.state.auth.store_user;
        },
      /* CARGA TODAS LAS TIENDAS */
        stores(){
          return this.$store.state.auth.user.stores;
        },

        status(){
            switch(this.data_store.status){
                case 'A' : return 'ACTIVE';
                break;
                case 'S' : return 'SUSPENDED';
                break;
                case 'DA' : return 'DENIED ACTIVATION';
                break;
                case 'V' : return 'VACATION';
                break;
                case 'ED' : return 'EVERYDAY';
                break;
                default : return 'PENDING ACTIVATION';
                break;
            }
        }
    },

    mounted() {
        for (let i = 0; i < this.stores.length; i++) {
            let store = this.stores[i].store;
            this.items.push({ text: store.name, value: store })
        }
        this.store_id = this.data_store
    },

    methods: {
      getColor (status) {
        if (status == 'ACTIVE') return 'green'
        else if (status == 'EVERYDAY') return 'purple'
        else if (status == 'SUSPENDED') return 'red'
        else if (status == 'DENIED ACTIVATION') return 'gray'
        else if (status == 'VACATION') return 'yellow'
        else return 'orange'
      },

      changeStore(){
        this.$store.commit('auth/changeStore', this.store_id);
      }
    }
  }
</script>