<template>
  <v-app>
    <nav-bar :name="title"></nav-bar>
      <v-container v-if="isStaff">
        <dash-admin></dash-admin>
      </v-container>
      <v-container v-else>
        <dash-clients></dash-clients>
      </v-container>

  <firma></firma>

  </v-app>
</template>

<script>
  import NavBar from '@/components/NavBar';
  import DashAdmin from '@/components/DashAdmin';
  import DashClients from '@/components/DashClients';
  import Firma from '@/components/Title';

  export default {
    components: {
      NavBar, DashAdmin, DashClients, Firma
    },
    data: () => ({
      title: 'TROD APP',
    }),

    computed: {
      isStaff(){
        if (this.$store.state.auth.user.is_staff)
          return true;

        return false;
      },
    },
  }
</script>